<template>
  <div id="app">
    <div class='screencontainer'>
     <div class="top-menu-bar">
      <label for="hamburger" class='border-menu'></label>
      <input type="checkbox" id="hamburger"/>

      <div class='poolsdropdown'>
        <button class='simplebutton dropdownHeader' >[swUSD]</button>
        <div class='dropdown'>
            <button class='simplebutton' @click = 'changeWallets'>Change wallet</button>
            <button  class='simplebutton' 
              v-show="['ledger', 'trezor'].includes(walletName)" 
              @click = 'changeAccounts'>Logout</button>
        </div>
      </div>

      <router-link to='/'>Pools</router-link>
      <a href="./dao/">DAO</a>
      <router-link :to="'/'+currentPool" v-show="currentPool !='susd'">Buy and sell</router-link>
      <router-link :to="'/' + currentPool + '/deposit'" v-show="currentPool !='susd'">Deposit</router-link>
      <router-link :to="'/' + currentPool + '/withdraw'">Withdraw</router-link>
      <div class='poolsdropdown right'>
        <span>?</span>
        <div class='dropdown'>
          <span class="simplebutton">
            <router-link to="/contracts">Contracts</router-link>
          </span>
          <span class='simplebutton'>
             <a target="_blank" href="https://uniswap.info/token/0xb8baa0e4287890a5f79863ab62b7f175cecbd433">Uniswap</a>
          </span>
          <span class='simplebutton'>
            <a target="_blank" href="https://github.com/swervefinance">git@</a>
          </span>
        </div>
      </div>

      <a target="_blank" class='showmobile' href="https://uniswap.info/token/0xb8baa0e4287890a5f79863ab62b7f175cecbd433">Uniswap</a>
      <a target="_blank" :href="'https://github.com/swervefinance'" class='showmobile' rel='noopener noreferrer'>git@</a>

      <button class='simplebutton showmobile' @click = 'changeWallets'>Change wallet</button>
      <button  class='simplebutton showmobile' 
        v-show="['ledger', 'trezor'].includes(walletName)" 
        @click = 'changeAccounts'>Logout</button>
        </button>
    </div>
    <div id="screen">
        <div :class="{'blue window': true, [$route.name]: true}">
            <h1><img :src="logoSrc" alt="🌀 Swerve"></h1>
        </div>
        <div class="error window half-width info" id="error-window" v-show='error'>
          {{error}}
        </div>
        <div class='info-message gentle-message window half-width gentle-message' v-show='hasConnectedWallet'>
          You haven't connected a wallet. <button @click='changeWallets'>Connect wallet</button>
        </div>
        <div class='simple-error window' v-show='plsReturn'>
          Your recent withdrawal from Swerve resulted in getting 1000 more USDT because of another user mistakenly transferring funds to the contract.
          If you wish to return them - please contact us on twitter.
        </div>
        <router-view/>
    </div>
    <balances-info
    :class = '{[$route.name]: true}'
    :bal_info = 'bal_info'
    :total = 'balTotal'
    :l_info = 'l_info'
    :totalShare = 'totalShare'
    :staked_info = 'staked_info'
    :totalStake = 'totalStake'
    :fee = 'fee'
    :admin_fee = 'admin_fee'
    :currencies = 'currencies'
    v-if="!['Stats', 'Root', 'CombinedStats'].includes($route.name)"/>
    </div>

  </div>
</template>

<script>
  import BalancesInfo from '../components/BalancesInfo.vue'
  import { getters, contract as currentContract, changeContract, poolMenu } from '../contract'
  import { onboard, changeWallets } from '../init'
  import allabis from '../allabis'

  const titles = {
      compound: 'Compounded',
      usdt: 'Tethered',
      swusd: 'Yield',
      busd: 'bUSD',
      susd: 'sUSD-yCurve old',
      susdv2: 'sUSD',
        pax: 'PAX',
        tbtc: 'TBTC',
        ren: 'renBTC',
        sbtc: 'sBTC',
    }

  export default {
    metaInfo() {
      return {
        title: 'Swerve.fi :: ' + titles[this.currentPool],
      }
    },
    data: () => ({
      gitBranches: {
        compound: 'compound',
        usdt: 'usdt',
        swusd: 'y',
        y: 'y',
        busd: 'busd',
        susd: 'susd_vulnerable',
        susdv2: 'susd_plain',
        pax: 'pax',
        tbtc: 'tbtc',
        ren: 'ren',
        sbtc: 'renbtc_sbtc',
      }
    }),
    components: {
      BalancesInfo,
    },
    computed: {
      allGetters() {
        return getters;
      },
      ...getters,
      poolMenu() {
        return poolMenu;
      },
      poolAddress() {
        return allabis[this.currentPool].swap_address
      },
      tokenAddress() {
        return allabis[this.currentPool].token_address
      },
      publicPath() {
        return process.env.BASE_URL
      },
      logoSrc() {
        if(!currentContract.swapbtc) return this.publicPath + 'logo_optimized.svg'
        else return this.publicPath + 'logo_ren_beta_optimized.svg'
      },
      hasConnectedWallet() {
        return this.default_account == '0x0000000000000000000000000000000000000000'
                && !['StatsDaily', 'Stats', 'Contracts'].includes(this.$route.name)
      },
      plsReturn() {
        return currentContract.currentContract.toLowerCase() == '0x72c20f89008729c91b6bb85f3104fda942494cef'.toLowerCase()
      },
    },
    methods: {
      changePools(pool) {
        changeContract(pool)
      },
      async changeWallets() {
        changeWallets()
      },
      async changeAccounts() {
        return onboard.accountSelect();
      },
    },
  }
</script>

<style>
  a.showmobile {
    display: none;
  }
  @media only screen and (min-device-width : 320px) and (max-device-width : 730px) {
    #hamburger:checked ~ a.showmobile {
      display: block;
    }
    .blue.window.half-width, .info-message.window.half-width {
      width: 90%;
    }
  }
  h1 > img {
    height: 52.125px;
  }
  .simple-error.window {
    box-shadow: none;
  }
  .CRV a:hover, .CRV a:visited {
    color: white;
  }
</style>
